@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

body {

    background: rgb(63,94,251);
    background: linear-gradient(28deg, rgba(63,94,251,1) 0%, rgba(0,0,0,1) 100%); 
    overflow-x:hidden;
}

.search-and-result {
    display:flex;
    flex-direction:row;
    align-items:flex-start;
    justify-content: space-between;
    width:100%;
    padding-bottom: 2vw;
    @include mobile-only {
        flex-wrap:wrap;
        flex-direction: column-reverse;
    }
    & input {
        width: 90%;
        height: 4.5vw;
        font-size: 2.5vw;
        outline: none;
        border:none;
        background-color: rgba(0, 0, 0, 0.698);
        color:white;
        box-sizing:border-box;
        
        @include mobile-only {
            font-size: 3vh;
            height: 6vh;
        }
    }
    & button {
        width: 10%;
        height: 4.5vw;
        font-size: 2.5vw;
        color: rgb(255, 255, 255);
        background-color: rgba(0, 0, 0, 0.698);

        border: none;
        outline:none;
        box-sizing:border-box;
        @include mobile-only {
            font-size: 3vh;
            height: 6vh;
        }
        &:hover {
            color:orange;
        }
    };
    &__search-days-container {
        width: 60%;
        display: flex;
        flex-direction:row;
        flex-wrap:wrap;
        align-items: flex-start;
        @include mobile-only{
            width: 100%;
        }
    }
}