@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.forecast {
    &__root-container {
        background-color:transparent;
        display:flex;
        flex-direction:row;
        flex-wrap:nowrap;
        width: 100vw;
    }
    &__cards-container {
        display: flex;
        flex-direction:row;
        flex-wrap:wrap;

    }
    &__card {
        display:flex;
        flex-direction: column;
        box-sizing: border-box;
        width: 22%;
        margin: 1%;
        padding-bottom: 1vw;
        box-sizing:border-box;
        background-color: rgba(255, 255, 255, 0.65);
        border-radius: 0.3vw;
        overflow:hidden;


        @include mobile-only {
            width: 46%;
            height: 22vh;
            font-size: 2vh;
            background-color: transparent;
            color:white;
            flex-direction:column;
            flex-wrap:nowrap;
            margin-top: 5vh;
            padding: 1vh;
            margin: 0;
            overflow:visible;
        }

        &--current {
            width: 40vw;
            color:white;
            background-color: transparent;
            @include mobile-only {
                width: 100%;
                height: auto;
                margin:0;

 
            }

        }
        &--selected {
            background-color:rgba(255, 149, 10, 0.746);
            border-radius: 1vh;
        }
        &__city {
            font-size: 2vw;
            text-align: left;
            @include mobile-only {
                font-size: 3vh;
                height: 6vh;
            }
        }
        &__parameters-container {
            display:flex;
            flex-direction:row;
            justify-content: space-evenly;
        }
        
        &__time-weather {
            display:flex;
            align-items:center;
            justify-content:space-evenly;
            background-color:black;
            padding: 0.5vw;
            height: 3.5vw;
            border-bottom:0.3vw solid rgb(0, 204, 255);
            @include mobile-only {
                background-color: transparent;
                height: auto;
                align-items:flex-start;
                width:100%;
                border:none;

            }
            &__time {
                font-size: 2vw;
                margin-right: 25%;
                color:white;
                @include mobile-only {
                    font-size: 5vh;
                    margin-right:-5%;
                    z-index: 99;
                    color:rgb(255, 255, 255);

                    padding: 0;
                    height: auto;
                    box-sizing:border-box;
                    text-shadow:2px 2px 2px rgb(148, 67, 1);

                }

            }
            &__icon {
                font-size: 3.5vw;
                color:rgb(223, 223, 223);
                @include mobile-only {
                    font-size: 10vh;

                }
                &--Rain {

                    background: linear-gradient(to bottom, rgb(255, 255, 255), rgb(50, 135, 204));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clouds {

                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(57, 79, 97));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clear {
                    background: linear-gradient(120deg, rgb(251, 255, 0), rgb(255, 196, 0));
                    color: transparent;
                    background-clip: text;  
                }
                &--Thunderstorm {
                    color: rgb(255, 51, 0);
                }
                &--Snow {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(0, 174, 255));
                    color: transparent;
                    background-clip: text;  
                }
                &--big {
                    font-size: 8vw;
                    margin-top: 2.7vw;
                    margin-left: 5vw;
                    @include mobile-only {
                        font-size: 10vh;
                        height: auto;
                        margin-top:0;
                        margin-left: 15vw;
                    }
                }
            }
        }
        &__temp {
            display:flex;
            align-items:center;
            padding-left: 1vw;
            &__icon {
                font-size:2vw;
                width: 2vw;
                color:red;  
                margin-right: 0.5vw; 
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right: 0.5vh; 

                }
            }
            &__value {
                font-size: 2.5vw;
                @include mobile-only {
                    font-size: 2.5vh;
                }
                &:after {
                    content: "°C";
                    margin-left: 0.5vw;
                }
            }
        }
        &__wind {
            display:flex;
            align-items:center;
            padding-left: 1vw;
            @include mobile-only {


            }
            &__icon {
                font-size:2vw;
                width: 2vw;
                color:rgb(0, 174, 255);  
                margin-right: 0.5vw; 
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right: 2vh;
                }
            }
            &__value {
                font-size: 2vw;
                @include mobile-only {
                    font-size: 2.5vh;
                }
                &:after {
                    content: "m/s";
                    margin-left: 0.5vw;
                    
                }
            }
        }
        &__wind-direction {
            display:flex;
            align-items:center;
            padding-left: 1vw;
            &__icon {
                font-size:2vw;
                width: 2vw;
                color:rgb(0, 255, 191);  
                margin-right: 0.5vw; 
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right: 2vh;
                }
            }
            &__value {
                font-size: 1vw;
                @include mobile-only {
                    font-size: 1.7vh;
                }
            }
        }

    }
    &__day-container {
        display:flex;
        align-items:center;
        justify-content:center;
        max-width: 100%;
        margin: 0;
        & > * {
            justify-content:flex-start;
        }
        &__date {
            font-size: 1.5vw;
            width: 10vw;
        }

    }
    &__day-forecasts {
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        width:100%;
        justify-content:center;
        overflow:hidden;
        height: max-content;
        padding-bottom: 30vh;
    }
    
}