@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.day-selector {
    display: flex;
    flex-direction:row;
    flex-wrap:nowrap;
    width: 100%;
    margin: 1%;
    justify-content:center;
    font-size: 1.2vw;  
    @include mobile-only {
        flex-direction: row;
        flex-wrap:wrap;


    }

    &__day {
        display:flex;
        flex-direction:column;
        padding: 0.2vw 0.1vw;
        background-color: rgba(0, 225, 255, 0.281);
        color: white;
        width: 16.6%;
        text-align:center;
        @include mobile-only {
        width: 16.6%;
        font-size: 2vh;
        margin-bottom: 0vh;
        align-items:center;
        flex-wrap:nowrap;
        justify-items:space-between;
        box-sizing:border-box;
        }
      &--current {
            background-color:rgba(255, 251, 0, 0.212);
            border-bottom: 0.5vw solid orange;
            margin-bottom: -0.5vw;
            @include mobile-only {
                margin-bottom: 0;
            }

        }
    }
}